import { useRouter } from 'next/router'
import Link from 'next/link'
import Navbar from '../../components/Navbar'

export default function Adaptation() {
  const router = useRouter()
  const { goal, experience, time, days, equipment, style } = router.query

  const options = [
    {
      id: 'conservative',
      title: 'Conservative (Safe & Steady)',
      emoji: 'üõ°Ô∏è',
      desc: [
        'Slow, controlled progression with very low injury risk.',
        'Ideal for beginners, people with joint pain or poor recovery.',
        'Smaller weight jumps, easier sets, and more recovery.'
      ]
    },
    {
      id: 'moderate',
      title: 'Moderate (Balanced & Effective)',
      emoji: '‚öñÔ∏è',
      desc: [
        'Well-balanced progression for most people.',
        'Coach-standard pacing with safe but challenging intensity.',
        'Smooth results without overwhelming fatigue.'
      ]
    },
    {
      id: 'aggressive',
      title: 'Aggressive (Fast & Intense)',
      emoji: 'üî•',
      desc: [
        'Fast weight jumps and high intensity.',
        'Best for experienced lifters with strong recovery.',
        'Pushes performance aggressively each week.'
      ]
    }
  ]

  return (
    <>
      <Navbar />
      <main className="container p-8 max-w-3xl">
        <h2 className="text-2xl font-semibold mb-4">How aggressively do you want to progress?</h2>

        <div className="grid md:grid-cols-2 gap-4">
          {options.map(o => (
            <Link
              key={o.id}
              href={`/onboard/summary?goal=${goal}&experience=${experience}&time=${time}&days=${days}&equipment=${equipment}&style=${style}&adapt=${o.id}`}
              className="card hover:scale-[1.02] transition"
            >
              <h3 className="font-semibold text-lg mb-1 flex items-center gap-2">
                <span>{o.emoji}</span> {o.title}
              </h3>

              <ul className="list-disc pl-5 text-sm text-slate-600 dark:text-slate-300">
                {o.desc.map((d, i) => (
                  <li key={i}>{d}</li>
                ))}
              </ul>
            </Link>
          ))}
        </div>
      </main>
    </>
  )
}
